body {
  margin: 0 auto;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(to bottom, #902d5d, #98466d, #9f5c7c, #a6718b, #ac8699);
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.characters__list {
  display: flex;
  max-width: 100vw;
  flex-wrap: wrap;
  margin-top: 50px;
  padding: 5px;
  justify-content: center; }
  @media all and (min-width: 1280px) {
    .characters__list {
      padding-left: 150px;
      padding-right: 150px; } }

.characters__list-item {
  list-style-type: none;
  margin: 10px;
  padding: 0;
  border: 1px solid #C1B400;
  border-radius: 10px;
  background-color: #6E1E45;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 0 rgba(0, 0, 0, 0.35); }

.characters__list-item-link {
  text-decoration: none;
  color: #C1B400; }

.message-results {
  text-align: center; }

.character-image {
  width: 163px;
  height: 227px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px; }

.character-name {
  font-size: 14px;
  text-align: center; }

.character-house {
  text-align: center;
  font-size: 12px; }

.detail-card {
  width: 180px;
  height: 360px;
  margin: 0 auto;
  border: 1px solid #C1B400;
  background-color: #6E1E45;
  border-radius: 10px;
  margin-bottom: 50px;
  margin-top: 50px;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 0 rgba(0, 0, 0, 0.35); }
  @media all and (min-width: 768px) {
    .detail-card {
      width: 500px;
      height: 250px;
      display: flex;
      margin-top: 100px; } }
  @media all and (min-width: 1280px) {
    .detail-card {
      width: 700px;
      height: 300px;
      display: flex; } }

.detail-card__image {
  width: 100%;
  height: 230px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px; }
  @media all and (min-width: 768px) {
    .detail-card__image {
      width: 200px;
      height: 100%;
      border-top-left-radius: 10px;
      border-bottom-left-radius: 10px;
      border-top-right-radius: 0px; } }

.detail-card__info {
  text-align: center;
  color: #C1B400; }
  @media all and (min-width: 768px) {
    .detail-card__info {
      padding-left: 20px;
      width: 100%;
      text-align: left; } }

.detail-card__name {
  font-size: 18px; }
  @media all and (min-width: 768px) {
    .detail-card__name {
      margin-bottom: 30px;
      margin-top: 50px;
      font-size: 18px; } }

.detail-card__house {
  margin: 0;
  font-size: 12px; }
  @media all and (min-width: 768px) {
    .detail-card__house {
      margin-bottom: 10px; } }

.detail-card__birth {
  margin: 0;
  font-size: 12px; }
  @media all and (min-width: 768px) {
    .detail-card__birth {
      margin-bottom: 10px; } }

.detail-card__patronus {
  margin: 0;
  font-size: 12px; }
  @media all and (min-width: 768px) {
    .detail-card__patronus {
      margin-bottom: 10px; } }

.detail-card__state {
  margin: 0;
  font-size: 12px;
  margin-bottom: 10px; }

.go-back {
  text-decoration: none;
  color: black;
  margin-left: 40%;
  font-size: 20px;
  border: 1px solid black;
  border-radius: 20px;
  padding: 7px 10px;
  margin-bottom: 40px; }
  @media all and (min-width: 768px) {
    .go-back {
      margin-left: 48%; } }

.gryffindor {
  height: 70px;
  width: 70px;
  background-image: url(../../static/media/Gryffindor.eba57e8c.png);
  -webkit-transform: translate(140px, -360px);
          transform: translate(140px, -360px);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain; }
  @media all and (min-width: 768px) {
    .gryffindor {
      height: 100px;
      width: 150px;
      position: relative;
      -webkit-transform: translate(25px, -15px);
              transform: translate(25px, -15px); } }

