  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  -webkit-transform: translate(140px, -360px);
          transform: translate(140px, -360px); }
  @media all and (min-width: 768px) {
    .ravenclaw {
      height: 100px;
      width: 150px;
      position: relative;
      float: right;
      -webkit-transform: translate(25px, -15px);
              transform: translate(25px, -15px); } }

.slytherin {
  height: 70px;
  width: 70px;
  background-image: url(../../static/media/Slytherin.db11dc31.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  -webkit-transform: translate(140px, -360px);
          transform: translate(140px, -360px); }
  @media all and (min-width: 768px) {
    .slytherin {
      height: 100px;
      width: 150px;
      position: relative;
      float: right;
      -webkit-transform: translate(25px, -15px);
              transform: translate(25px, -15px); } }

.app-input {
  border: 0px solid;
  border-radius: 20px;
  width: 250px;
  height: 30px; }
  @media all and (min-width: 768px) {
    .app-input {
      width: 300px;
      height: 30px; } }

.app-title {
  text-align: center;
  color: #C1B400;
  font-size: 40px;
  margin-bottom: 50px;
  margin-top: 70px;
  font-family: 'Cinzel', serif; }
  @media all and (min-width: 768px) {
    .app-title {
      font-size: 40px; } }

.app-filter {
  display: flex;
  justify-content: center; }

